import React, { useState } from "react";
import {
	StyleSheet,
	Text,
	View,
	Image,
	TouchableOpacity,
	FlatList,
} from "react-native";
const imageLink =
	"data:image/jpeg;base64,";
export default function EventAttendees({ navigation, route }) {
	const { attendees } = route.params;
	console.log(attendees);
	return (
		<View style={styles.container}>
			<View style={styles.body}>
				<Text style={styles.title}>Event Attendees</Text>
				<FlatList
					style={styles.container}
					enableEmptySections={true}
					data={attendees}
					keyExtractor={(item, index) => {
						return item.attendee_id.toString();
					}}
					renderItem={({ item }) => {
						return (
							<TouchableOpacity
								onPress={() =>
									navigation.navigate("UserProfile", {
										username: item.attendee,
									})
								}
							>
								<View style={styles.orangeLine} />
								<View style={styles.box}>
									<Image style={styles.image} source={{ uri: imageLink }} />
									<Text style={styles.username}>{item.name}</Text>
								</View>
							</TouchableOpacity>
						);
					}}
				/>
			</View>
		</View>
	);
}
const styles = StyleSheet.create({
	image: {
		width: 80,
		height: 80,
		borderRadius: 63,
		marginBottom: 10,
	},
	body: {
		padding: 30,
	},
	box: {
		padding: 5,
		borderRadius: 10,
		borderWidth: 2,
		borderColor: "#FF8C00",
		marginTop: 5,
		marginBottom: 5,
		backgroundColor: "white",
		flexDirection: "row",
		shadowColor: "black",
		shadowOpacity: 0.6,
		shadowOffset: {
			height: 1,
			width: -2,
		},
		elevation: 2,
	},
	username: {
		color: "black",
		fontSize: 20,
		alignSelf: "center",
		marginLeft: 30,
		fontWeight: "bold",
	},
	title: {
		color: "#E86B3A",
		fontSize: 30,
		alignSelf: "center",
		fontWeight: "bold",
		marginBottom: 5,
	},
	container: {
		height: "100%",
	},
});
